<script setup lang="ts">
import CardList from '@/components/story/CardList.vue'
import { onBeforeMount, ref,provide } from 'vue'
import { StoryApi } from './apis'
import { Story } from './type';
import { StoryContextKey } from './context';
import  Step  from '../../../components/Step.vue'

const list = ref<Array<Omit<Story, 'content' | 'prompt' | 'rules'> >>([])
onBeforeMount(async () => {
  list.value = await StoryApi.getList()
})


const activeKeyRef = ref<number>(0)

provide(StoryContextKey,{
  activeKeyRef,
  handleUpdateActiveKey:(index)=>{
    activeKeyRef.value = index
  }
})


</script>
<template>
  <div class="list">
    <Step :active-key="activeKeyRef" />
    <div class="title"><span>Starting </span> <span> Your Journey </span></div>
    <CardList :list="list" />
  </div>

  <router-view></router-view>
</template>
